import { useEffect } from 'react';
import axios from 'axios';
import {ip, port} from './../url';
import qs from 'qs';
// qs     ,       .
// stringify (obj, [, sep='&', eqe='=']) :     .
// parse(str[, set='&', eq='=']) :     .
// ex)   : { q: 'nodejs', aqs : chrome.0.135i4j24, ...},   : q=nodejs&aqs=chrome.0.135i4j24 ...
function SocialLogin({ history, location }) {
  //history location    .
  //location   URL   .
  useEffect(() => {
    console.log('')
    async function getToken() {
      console.log(`location.search=${location.search}`)
      const { code } = qs.parse(location.search, {
        ignoreQueryPrefix: true,
      });
      try {
        //   express  JWT  .
        const accessToken = await axios.post(`${ip}${port}/github/auth`, {
          code,
        })
          console.log(accessToken.data);
        //  JWT  .
        localStorage.setItem('token', accessToken.data);
        history.push('/'); //    
        console.log('  ')
      } catch (error) {
        // history.push('/error'); // api    
      }
    }
    getToken();
  }, [location, history]);
  return null; //        .
}
export default SocialLogin;